.container {
  position: relative;
  border: 1px solid var(--color-blue);
  border-radius: 20px;
  overflow: hidden;
}

.input {
  padding: 4px 48px 4px 12px;
  height: 40px;
  outline: none;

  font-family: 'Manrope';
  font-size: 16px;
  line-height: 1.37;
}

.editIcon {
  position: absolute;
  cursor: pointer;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
}
